.preview__link:link,
.preview__link:visited {
  display: flex;
  align-items: center;
  padding: 1.5rem 3.25rem;
  transition: all 0.3s;
  border-right: 1px solid #fff;
  text-decoration: none;
}

.preview__link:hover {
  background-color: #f5f5f5; /* Replace with $color-grey-light-1 */
  transform: translateY(-2px);
}

.preview__link--active {
  background-color: #f5f5f5; /* Replace with $color-grey-light-1 */
}

.preview__fig {
  flex: 0 0 5.8rem;
  border-radius: 50%;
  overflow: hidden;
  height: 5.8rem;
  margin-right: 2rem;
  position: relative;
  backface-visibility: hidden;
}

.preview__fig::before {
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(to right bottom, #ff7c7c, #fca542); /* Replace with $color-grad-1 and $color-grad-2 */
  opacity: 0.4;
}

.preview__fig img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.3s;
}

.preview__data {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 2rem;
  row-gap: 0.1rem;
  align-items: center;
}

.preview__title {
  grid-column: 1/-1;
  font-size: 1.45rem;
  color: #fca542; /* Replace with $color-primary */
  text-transform: uppercase;
  font-weight: 600;
  text-overflow: ellipsis;
  max-width: 25rem;
  white-space: nowrap;
  overflow: hidden;
}

.preview__publisher {
  font-size: 1.15rem;
  color: #918581; /* Replace with $color-grey-dark-2 */
  text-transform: uppercase;
  font-weight: 600;
}

.preview__user-generated {
  background-color: #d1d1d1; /* Replace with darken($color-grey-light-2, 2%) */
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2rem;
  width: 2rem;
  border-radius: 10rem;
  margin-left: auto;
  margin-right: 1.75rem;
}

.preview__user-generated svg {
  height: 1.2rem;
  width: 1.2rem;
  fill: #ff0a0a; /* Replace with $color-primary */
}
