.btn {
    background-image: var(--gradient); /* Replace with your actual gradient variable */
    border-radius: 10rem;
    border: none;
    text-transform: uppercase;
    color: #fff;
    cursor: pointer;
    display: flex;
    align-items: center;
    transition: all 0.2s;
  }
  
  .btn:hover {
    transform: scale(1.05);
  }
  
  .btn:focus {
    outline: none;
  }
  
  .btn > *:first-child {
    margin-right: 1rem;
  }
  
  .btn__small,
  .btn__small:link,
  .btn__small:visited {
    background-image: var(--gradient); /* Replace with your actual gradient variable */
    border-radius: 10rem;
    border: none;
    text-transform: uppercase;
    color: #fff;
    cursor: pointer;
    display: flex;
    align-items: center;
    transition: all 0.2s;
    font-size: 1.4rem;
    font-weight: 600;
    padding: 1.25rem 2.25rem;
  }
  
  .btn__small svg {
    height: 1.75rem;
    width: 1.75rem;
    fill: currentColor;
  }
  
  .btn__inline {
    color: var(--color-primary);
    font-size: 1.3rem;
    font-weight: 600;
    border: none;
    background-color: var(--color-grey-light-1);
    padding: 0.8rem 1.2rem;
    border-radius: 10rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    transition: all 0.2s;
  }
  
  .btn__inline svg {
    height: 1.6rem;
    width: 1.6rem;
    fill: currentColor;
    margin: 0 0.2rem;
  }
  
  .btn__inline span {
    margin: 0 0.4rem;
  }
  
  .btn__inline:hover {
    color: var(--color-grad-2);
    background-color: var(--color-grey-light-2);
  }
  
  .btn__inline:focus {
    outline: none;
  }
  
  .btn__round {
    background-image: var(--gradient);
    border-radius: 50%;
    border: none;
    cursor: pointer;
    height: 4.5rem;
    width: 4.5rem;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .btn__round:hover {
    transform: scale(1.07);
  }
  
  .btn__round:focus {
    outline: none;
  }
  
  .btn__round svg {
    height: 2.5rem;
    width: 2.5rem;
    fill: #fff;
  }
  
  .btn__tiny {
    height: 2rem;
    width: 2rem;
    border: none;
    background: none;
    cursor: pointer;
  }
  
  .btn__tiny svg {
    height: 100%;
    width: 100%;
    fill: var(--color-primary);
    transition: all 0.3s;
  }
  
  .btn__tiny:focus {
    outline: none;
  }
  
  .btn__tiny:hover svg {
    fill: var(--color-grad-2);
    transform: translateY(-1px);
  }
  
  .btn__tiny:active svg {
    fill: var(--color-grad-2);
    transform: translateY(0);
  }
  
  .btn__tiny:not(:last-child) {
    margin-right: 0.3rem;
  }
  
  .heading--2 {
    font-size: 2rem;
    font-weight: 700;
    color: var(--color-primary);
    text-transform: uppercase;
    margin-bottom: 2.5rem;
    text-align: center;
  }
  
  .link:link,
  .link:visited {
    color: var(--color-grey-dark-2);
  }
  
  .spinner {
    margin: 5rem auto;
    text-align: center;
  }
  
  .spinner svg {
    height: 6rem;
    width: 6rem;
    fill: var(--color-primary);
    animation: rotate 2s infinite linear;
  }
  
  @keyframes rotate {
    0% {
      transform: rotate(0);
    }
  
    100% {
      transform: rotate(360deg);
    }
  }
  
  .message,
  .error {
    max-width: 40rem;
    margin: 0 auto;
    padding: 5rem 4rem;
    display: flex;
  }
  
  .message svg,
  .error svg {
    height: 3rem;
    width: 3rem;
    fill: var(--color-primary);
    transform: translateY(-0.3rem);
  }
  
  .message p,
  .error p {
    margin-left: 1.5rem;
    font-size: 1.8rem;
    line-height: 1.5;
    font-weight: 600;
  }
  