.recipe {
    background-color: #f0f0f0; /* Replace with $color-grey-light-1 */
  }
  
  .recipe__fig {
    height: 32rem;
    position: relative;
    transform-origin: top;
  }
  
  .recipe__fig::before {
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-image: linear-gradient(to right bottom, #ff7c7c, #fca542); /* Replace with $color-grad-1 and $color-grad-2 */
    opacity: 0.6;
  }
  
  .recipe__img {
    width: 100%;
    display: block;
    height: 100%;
    object-fit: cover;
  }
  
  .recipe__title {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 20%) skewY(-6deg);
    color: #fff;
    font-weight: 700;
    font-size: 3.25rem;
    text-transform: uppercase;
    width: 50%;
    line-height: 1.95;
    text-align: center;
  }
  
  .recipe__title span {
    -webkit-box-decoration-break: clone;
    box-decoration-break: clone;
    padding: 1.3rem 2rem;
    background-image: linear-gradient(to right bottom, #ff7c7c, #fca542); /* Replace with $color-grad-1 and $color-grad-2 */
  }
  
  @media only screen and (max-width: 768px) { /* Replace with $bp-medium */
    .recipe__title {
      width: 70%;
    }
  }
  
  .recipe__details {
    display: flex;
    align-items: center;
    padding: 7.5rem 8rem 3.5rem 8rem;
  }
  
  .recipe__info {
    font-size: 1.65rem;
    text-transform: uppercase;
    display: flex;
    align-items: center;
  }
  
  .recipe__info:not(:last-child) {
    margin-right: 4.5rem;
  }
  
  .recipe__info-icon {
    height: 2.35rem;
    width: 2.35rem;
    fill: #fca542; /* Replace with $color-primary */
    margin-right: 1.15rem;
  }
  
  .recipe__info-data {
    margin-right: 0.5rem;
    font-weight: 700;
  }
  
  .recipe__info-buttons {
    display: flex;
    margin-left: 1.6rem;
    transform: translateY(-1px);
  }
  
  .recipe__user-generated {
    background-color: #e6e6e6; /* Replace with darken($color-grey-light-2, 2%) */
    display: flex;
    align-items: center;
    justify-content: center;
    height: 4rem;
    width: 4rem;
    border-radius: 10rem;
    margin-left: auto;
    margin-right: 1.75rem;
  }
  
  .recipe__user-generated svg {
    height: 2.25rem;
    width: 2.25rem;
    fill: #fca542; /* Replace with $color-primary */
  }
  
  .recipe__ingredients {
    padding: 5rem 8rem;
    font-size: 1.6rem;
    line-height: 1.4;
    background-color: #f5f5f5; /* Replace with $color-grey-light-2 */
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .recipe__ingredient-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2.5rem 3rem;
    list-style: none;
  }
  
  .recipe__ingredient {
    display: flex;
  }
  
  .recipe__icon {
    height: 2rem;
    width: 2rem;
    fill: #fca542; /* Replace with $color-primary */
    margin-right: 1.1rem;
    flex: 0 0 auto;
    margin-top: 0.1rem;
  }
  
  .recipe__quantity {
    margin-right: 0.5rem;
    flex: 0 0 auto;
  }
  
  .recipe__directions {
    padding: 5rem 10rem;
    padding-bottom: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .recipe__directions-text {
    font-size: 1.7rem;
    text-align: center;
    margin-bottom: 3.5rem;
    color: #918581; /* Replace with $color-grey-dark-2 */
  }
  
  .recipe__publisher {
    font-weight: 700;
  }
  